export default {
	data() {
		return {
			news: []
		}
	},
	onLoad: function() {
		uni.showLoading({
			title:"加载中。。。"
		}),
		uni.request({
			url:'https://unidemo.dcloud.net.cn/api/news',
			method:'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {
				
			},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e) {
			var newsid = e.currentTarget.dataset.newsid;
			console.log(newsid);
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	},
	
}
